.experience__details,
.experience__input {
    display: none;
    opacity: 0;
    // transition: height 2s;
}

.experience__input:checked +
.experience__title +
.experience__details {
    display: inherit;
    opacity: 1;
    transition: height 2s;
}

.experience__header {
    color: $color-periwinkle-gray;
    @extend %header;
    word-break: break-all;
}

.experience__title {
    background-color: $color-gumbo;
    color: $color-white;
    display: flex;
    justify-content: space-between;
    font-size: 1.3rem;
    padding: 0.7rem;
    cursor: pointer;
}

.experience__details {
    border: 0.2rem solid $color-gumbo;
}

.experience__top {
    display: flex;
    justify-content: space-between;
}

.experience__date {
    font-style: italic;
}

.experience__company {
    font-weight: bold;
}

.experience__date,
.experience__company {
    font-size: 1.1rem;
    padding: 0.7rem;
}

.experience__responsibility {
    padding: 0.3rem 1rem;
    font-size: 1rem;
    line-height: 1.2;

    &:nth-child(odd) {
        background-color: $color-fantasy;
    }

    &:nth-child(even) {
        background-color: $color-twilight;
    }
}

.my-cv__icon--plus {
    float: right;
    // to enable the click on plus icon
    pointer-events: none;
    // transition event must be placed here for display to take place during opening and closing
    transition: all 0.5s ease;
}

.experience__input:checked +
.experience__title {
    .my-cv__icon--plus {
        // change the plus to "X"
        transform: rotateZ(45deg);
        pointer-events: all;
        cursor: pointer;
    }
}