.landing-page {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: radial-gradient(#8a8fa2, #415a67);
    text-align: center;
    word-break: break-all;
    word-wrap: normal;
}

.landing-page__wrapper {
    text-align: center;
}

.landing-page__name {
    font-size: 2rem;
    text-transform: uppercase;
}

.landing-page__title {
    font-size: 1.7rem;
    text-transform: capitalize;
}

.landing-page__social {
    display: flex;
    margin-top: 3rem;
    justify-content: center;
}

.landing-page__label {
    color: $color-white;
    padding: 0.1rem 0.3rem;
    font-size: 2rem;
    cursor: pointer;
}

.landing-page__box-radio:checked +
.landing-page__box-message {
    // border: 1rem solid red;
    // padding: 1rem 0.5rem;
    display: inherit;
}

.landing-page__box-radio,
.landing-page__box-message {
    display: none;
}

#default-message-radio:checked +
.landing-page__box-message +
.landing-page__box-header {
    display: none;
}

.landing-page__box-body {
    display: inline-block;
    border: 0.2rem solid $color-white;
    border-radius: 0.5rem;
    padding: 0.8rem;
    position: relative;
    margin-top: 0.5rem;
}

.landing-page__box-header {
    position: absolute;
    left: 10%;
    right: 10%;
    top: 100%;
    display: flex;
    justify-content: space-between;
}

.landing-page__box-info,
.landing-page__box-close {
    background: $color-white;
    padding: 0.4rem;
    white-space: nowrap;
    line-height: 1.5rem;
    overflow: hidden;
    text-overflow: ellipsis;
}

.landing-page__box-info {
    border-bottom-left-radius: 0.5rem;
    flex-grow: 1;
}

.landing-page__box-close {
    border-bottom-right-radius: 0.5rem;
    cursor: pointer;
    width: 2.5rem;
    font-size: 1.5rem;
}

.landing-page__box-link,
.landing-page__box-link:visited,
.landing-page__box-message {
    color: $color-white;
}

.landing-page__box-link,
.landing-page__box-link:visited {
    font-size: 1.4rem;
    white-space: nowrap;
    text-decoration: none;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80vmin;
    display: block;
}

html[dir="rtl"] {
    .landing-page__box-info {
        order: 1;
    }
}