.employer__header {
    color: $color-periwinkle-gray;
    @extend %header;
}

.employer__image {
    // border: 0.1rem solid red;
    margin: 0 auto;
    width: 200px;
    height: 150px;
    background-image: url(/asset/image/client/employer.png);

    &:nth-child(2) {
        background-position-y: -150px;
    }

    &:nth-child(3) {
        background-position-y: -300px;
    }
}