* {
    box-sizing: border-box;
}

html, body {
    height: 100%;
    font-family: Arial, Helvetica, 'sans-serif';
}

html {
    // Values in calc() explained
    // 12px -> minimum font size
    // 24-12 -> (max font-size - min font-size)
    // 100 vmin -> the smallest value of the width and height of the viewport
    // 400px -> min screen size
    // 800 - 400 -> max screen suze - min screen size
    font-size: calc(12px + (24-12) * ((100vmin - 400px) / (800 - 400)));
}